<template>
   <div v-if="restaurant.Cuisines && restaurant.Cuisines.length">
  <div v-for="(i) in restaurant.Cuisines" :key="i.id">
<div class="menu-widget" id="2" v-if="i.Items && i.Items.length">
    <div class="widget-heading">
        <h3 class="widget-title text-dark" v-b-toggle="'collapse' + i.id">
            {{ i.name | uppercase }}
            <b-link v-b-toggle="'collapse' + i.id" class="btn btn-link pull-right">
                <span class="fa fa-angle-right arrow pull-right"></span>
                <i class="fa fa-angle-down arrow pull-right"></i>
            </b-link>
        </h3>
        <div class="clearfix"></div>
    </div>
    <b-collapse :id="'collapse' + i.id">
        <div class="food-item" v-for="(item) in i.Items" :key="item.id" v-if="item.disponible == true">
            <b-row>
                <div class="col-xs-12 col-sm-12 col-lg-8">
                    <div class="rest-logo pull-left">
                        <a class="restaurant-logo pull-left" v-if="item.thumb">
                          <!-- Food Logo 100x80 -->
                            <img :src="item.thumb" :width="100" :height="80" :alt="item.name">
                        </a>
                    </div>
                    <!-- end:Logo -->
                    <div class="rest-descr">
                        <h6>
                            <a>{{ item.name }}</a>
                        </h6>
                        <p>{{ item.ingredients }}</p>
                    </div>
                    <!-- end:Description -->
                </div>
                <!-- end:col -->
                <div class="col-xs-12 col-sm-12 col-lg-4 pull-right item-cart-info">
                    <span class="price pull-left">R$ {{ item.price }}</span>
                    <b-link v-b-modal.order-modal class="btn btn-small btn btn-success pull-right"><i class="fa fa-cart-plus"></i></b-link>
                </div>
            </b-row>
            <!-- end:row -->
        </div>
        <!-- end:Food item -->
    </b-collapse>
    <!-- end:Collapse -->
  </div>
</div>
</div>
<!-- end:Widget menu -->
</template>

<script>
export default {
  props: ['items', 'restaurant'],
  data () {
    return {}
  },
  filters: {
    uppercase: function (value) {
      return value.toUpperCase()
    }
  }
}
</script>

<style scoped>
.widget-heading {
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.menu-widget .widget-heading {
    border-bottom: none;
    margin-bottom: 10px;
}

.widget-heading .widget-title {
    font-size: 20px;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;

    color: #7b7b7b;
    
    font-weight: 400;
    
    text-transform: uppercase;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.widget-heading .widget-title .btn {
    padding: 0px 24px;
}

.menu-widget,
.widget {
    border: 1px solid #eaebeb;
    background: #f2f3f3;
    border-radius: 2px;
    position: relative;
    margin-bottom: 20px;
    cursor: pointer;
}

.menu-widget .food-item {
    padding: 15px;
        background: white;
}

.menu-widget .food-item:first-of-type {
    border-bottom: none;
}

.menu-widget .food-item .restaurant-logo {
    max-width: 100px;
    height: auto;
    display: block;
}

.menu-widget .food-item .restaurant-logo img {
    max-width: 100%;
    display: block;
}

.menu-widget .food-item:first-of-type {
    border-top: 1px solid #eaebeb;
}

.menu-widget .food-item:last-of-type {
    border-bottom: transparent;
}

.menu-widget .food-item.white {
    background-color: #fff;
}

.menu-widget .food-item .rest-descr {
    padding-left: 115px;
    padding-top: 0;
}

.menu-widget .food-item .rest-descr h6 a {
    color: #25282b;
    font-size: 18px;
    font-weight: 600;
}

.menu-widget .food-item .item-cart-info {
    padding-top: 20px;
}

.menu-widget .food-item .price {
    font-weight: 600;
    color: #000;
    position: relative;
    margin-top: 6px;
}

.arrow {
    font-size: 44px;
    position: absolute;
    margin-left: 0px;
    margin-top: -7px;
    color: #ffb42d;
}

a[aria-expanded=true] .fa-angle-right {
    display: none;
}

a[aria-expanded=false] .fa-angle-down {
    display: none;
}
</style>



